import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { useHistory } from 'react-router';
import { CheckIcon } from '@heroicons/react/outline';
import Loading from '../../UI/Loading';

const hobbyFeatures = [
  'Pariatur quod similique',
  'Sapiente libero doloribus',
  'Vel ipsa esse repudiandae',
];
const scaleFeatures = [
  'Pariatur quod similique',
  'Sapiente libero doloribus',
  'Vel ipsa esse repudiandae',
];
const growthFeatures = [
  'Quia rem est sed impedit magnam',
  'Dolorem vero ratione voluptates',
  'Qui sed ab doloribus voluptatem dolore',
  'Laborum commodi molestiae id et fugiat',
  'Nam ut ipsa nesciunt culpa modi dolor',
];

const PricingSection = ({ auth: { isAuthenticated }, products }) => {
  const [featuredProducts, setFeaturedProducts] = useState([]);
  const [mostPopularProduct, setMostPopularProduct] = useState();
  const history = useHistory();
  useEffect(() => {
    const featured = products.filter(
      (product) => product.featured && !product.most_popular
    );
    setFeaturedProducts(featured);
    const mostPopular = products.find((product) => product.most_popular);
    setMostPopularProduct(mostPopular);
  }, [products]);
  const addToCart = (productId) => {
    if (isAuthenticated) {
      let cartItemsStorage = [];
      let cartQuantitiesStorage = [];
      if (localStorage.getItem('cart_products')) {
        cartItemsStorage = JSON.parse(localStorage.getItem('cart_products'));
      }
      if (localStorage.getItem('cart_quantities')) {
        cartQuantitiesStorage = JSON.parse(
          localStorage.getItem('cart_quantities')
        );
      }
      cartItemsStorage.push(productId);
      cartQuantitiesStorage.push(1);
      localStorage.setItem('cart_products', JSON.stringify(cartItemsStorage));
      localStorage.setItem(
        'cart_quantities',
        JSON.stringify(cartQuantitiesStorage)
      );
      history.push('/cart');
    } else {
      history.push('/login');
    }
  };

  if (!featuredProducts.length > 0) {
    return <Loading />;
  }
  return (
    <div id='pricing' className='bg-gray-900'>
      <div className='pt-12 px-4 sm:px-6 lg:px-8 lg:pt-20'>
        <div className='text-center'>
          <h2 className='text-lg leading-6 font-semibold text-gray-300 uppercase tracking-wider'>
            Pricing
          </h2>
          <p className='mt-2 text-3xl font-extrabold text-white sm:text-4xl lg:text-5xl'>
            The right price for you, whoever you are
          </p>
          <p className='mt-3 max-w-4xl mx-auto text-xl text-gray-300 sm:mt-5 sm:text-2xl'>
            Lorem ipsum dolor sit, amet consectetur adipisicing elit. Velit
            numquam eligendi quos odit doloribus molestiae voluptatum.
          </p>
        </div>
      </div>

      <div className='mt-16 bg-white pb-12 lg:mt-20 lg:pb-20'>
        <div className='relative z-0'>
          <div className='absolute inset-0 h-5/6 bg-gray-900 lg:h-2/3' />
          <div className='max-w-7xl mx-auto px-4 sm:px-6 lg:px-8'>
            <div className='relative lg:grid lg:grid-cols-7'>
              <div className='mx-auto max-w-md lg:mx-0 lg:max-w-none lg:col-start-1 lg:col-end-3 lg:row-start-2 lg:row-end-3'>
                <div className='h-full flex flex-col rounded-lg shadow-lg overflow-hidden lg:rounded-none lg:rounded-l-lg'>
                  <div className='flex-1 flex flex-col'>
                    <div className='bg-white px-6 py-10'>
                      <div>
                        <h3
                          className='text-center text-2xl font-medium text-gray-900'
                          id='tier-hobby'
                        >
                          {featuredProducts[0].name}
                        </h3>
                        <div className='mt-4 flex items-center justify-center'>
                          <span className='px-3 flex items-start text-6xl tracking-tight text-gray-900'>
                            <span className='font-extrabold'>
                              {featuredProducts[0].unit_price}
                            </span>
                          </span>
                          <span className='mt-2 mr-2 text-4xl font-medium'>
                            €
                          </span>
                        </div>
                      </div>
                    </div>
                    <div className='flex-1 flex flex-col justify-between border-t-2 border-gray-100 p-6 bg-gray-50 sm:p-10 lg:p-6 xl:p-10'>
                      <ul className='space-y-4'>
                        {hobbyFeatures.map((feature) => (
                          <li key={feature} className='flex items-start'>
                            <div className='flex-shrink-0'>
                              <CheckIcon
                                className='flex-shrink-0 h-6 w-6 text-green-500'
                                aria-hidden='true'
                              />
                            </div>
                            <p className='ml-3 text-base font-medium text-gray-500'>
                              {feature}
                            </p>
                          </li>
                        ))}
                      </ul>
                      <div className='mt-8'>
                        <div className='rounded-lg shadow-md'>
                          <button
                            onClick={() => addToCart(featuredProducts[0].id)}
                            className='block w-full text-center rounded-lg border border-transparent bg-white px-6 py-3 text-base font-medium text-indigo-600 hover:bg-gray-50'
                            aria-describedby='tier-hobby'
                          >
                            Join now
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className='mt-10 max-w-lg mx-auto lg:mt-0 lg:max-w-none lg:mx-0 lg:col-start-3 lg:col-end-6 lg:row-start-1 lg:row-end-4'>
                <div className='relative z-10 rounded-lg shadow-xl'>
                  <div
                    className='pointer-events-none absolute inset-0 rounded-lg border-2 border-indigo-600'
                    aria-hidden='true'
                  />
                  <div className='absolute inset-x-0 top-0 transform translate-y-px'>
                    <div className='flex justify-center transform -translate-y-1/2'>
                      <span className='inline-flex rounded-full bg-indigo-600 px-4 py-1 text-sm font-semibold tracking-wider uppercase text-white'>
                        Most popular
                      </span>
                    </div>
                  </div>
                  <div className='bg-white rounded-t-lg px-6 pt-12 pb-10'>
                    <div>
                      <h3
                        className='text-center text-3xl font-semibold text-gray-900 sm:-mx-6'
                        id='tier-growth'
                      >
                        {mostPopularProduct.name}
                      </h3>
                      <div className='mt-4 flex items-center justify-center'>
                        <span className='px-3 flex items-start text-6xl tracking-tight text-gray-900 sm:text-6xl'>
                          <span className='font-extrabold'>
                            {mostPopularProduct.unit_price}
                          </span>
                        </span>
                        <span className='mt-2 mr-2 text-4xl font-medium'>
                          €
                        </span>
                      </div>
                    </div>
                  </div>
                  <div className='border-t-2 border-gray-100 rounded-b-lg pt-10 pb-8 px-6 bg-gray-50 sm:px-10 sm:py-10'>
                    <ul className='space-y-4'>
                      {growthFeatures.map((feature) => (
                        <li key={feature} className='flex items-start'>
                          <div className='flex-shrink-0'>
                            <CheckIcon
                              className='flex-shrink-0 h-6 w-6 text-green-500'
                              aria-hidden='true'
                            />
                          </div>
                          <p className='ml-3 text-base font-medium text-gray-500'>
                            {feature}
                          </p>
                        </li>
                      ))}
                    </ul>
                    <div className='mt-10'>
                      <div className='rounded-lg shadow-md'>
                        <button
                          onClick={() => addToCart(mostPopularProduct.id)}
                          className='block w-full text-center rounded-lg border border-transparent bg-indigo-600 px-6 py-4 text-xl leading-6 font-medium text-white hover:bg-indigo-700'
                          aria-describedby='tier-growth'
                        >
                          Join now
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className='mt-10 mx-auto max-w-md lg:m-0 lg:max-w-none lg:col-start-6 lg:col-end-8 lg:row-start-2 lg:row-end-3'>
                <div className='h-full flex flex-col rounded-lg shadow-lg overflow-hidden lg:rounded-none lg:rounded-r-lg'>
                  <div className='flex-1 flex flex-col'>
                    <div className='bg-white px-6 py-10'>
                      <div>
                        <h3
                          className='text-center text-2xl font-medium text-gray-900'
                          id='tier-scale'
                        >
                          {featuredProducts[1].name}
                        </h3>
                        <div className='mt-4 flex items-center justify-center'>
                          <span className='px-3 flex items-start text-6xl tracking-tight text-gray-900'>
                            <span className='font-extrabold'>
                              {featuredProducts[1].unit_price}{' '}
                            </span>
                          </span>
                          <span className='mt-2 mr-2 text-4xl font-medium'>
                            €
                          </span>
                        </div>
                      </div>
                    </div>
                    <div className='flex-1 flex flex-col justify-between border-t-2 border-gray-100 p-6 bg-gray-50 sm:p-10 lg:p-6 xl:p-10'>
                      <ul className='space-y-4'>
                        {scaleFeatures.map((feature) => (
                          <li key={feature} className='flex items-start'>
                            <div className='flex-shrink-0'>
                              <CheckIcon
                                className='flex-shrink-0 h-6 w-6 text-green-500'
                                aria-hidden='true'
                              />
                            </div>
                            <p className='ml-3 text-base font-medium text-gray-500'>
                              {feature}
                            </p>
                          </li>
                        ))}
                      </ul>
                      <div className='mt-8'>
                        <div className='rounded-lg shadow-md'>
                          <button
                            onClick={() => addToCart(featuredProducts[1].id)}
                            className='block w-full text-center rounded-lg border border-transparent bg-white px-6 py-3 text-base font-medium text-indigo-600 hover:bg-gray-50'
                            aria-describedby='tier-scale'
                          >
                            Join now
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => ({
  auth: state.auth,
});

export default connect(mapStateToProps)(PricingSection);
